function handleClick() {
  window.location.assign('/Calculator');
}
export default class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      password: ""
    };
  }

  validateForm() {
    return this.state.email.length > 0 && this.state.password.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  handleSubmit = async event => {
    event.preventDefault();
    try {
      await Auth.signIn(this.state.email, this.state.password);
      alert("Logged in");
    } catch (e) {
      alert("Sorry that didn't work! \nWe're still in dev, \nbut you can still try the Calculator free!");
    }
  }


  render() {
    return (
      <div className="Login">
        {/* <h1> Welcome to Prepit</h1> */}
        <p> Please Login / Signup to get started</p>
        <form onSubmit={this.handleSubmit}>
          <FormGroup controlId="email" bsSize="large">
            <InputGroup 
              className="mb-3"
              size="lg"
              autoFocus
              type="email"
              value={this.state.email}
              onChange={this.handleChange}
              >
              <InputGroup.Prepend>
                <InputGroup.Text>
                  Email
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl />
            </InputGroup>
          </FormGroup>
          <FormGroup controlId="password" bsSize="large">
            <InputGroup 
            className="mb-3"
            size="lg"
            value={this.state.password}
            onChange={this.handleChange}
            type="password"
            >
                <InputGroup.Prepend>
                  <InputGroup.Text>
                    Password
                  </InputGroup.Text>
                </InputGroup.Prepend>
              <FormControl />
            </InputGroup>
          </FormGroup>
          <Button
            className="ButtonLogin"
            block
            bsSize="large"
            disabled={!this.validateForm()}
            type="submit"
          >
            Login
          </Button>
          <Button
            className="ButtonSignup"
            block
            bsSize="large"
            type="signup"
            variant="secondary"
          >
            Signup
          </Button>
          <p
          style={{ marginTop: '40px', fontStyle: 'italic'}}
          >
            - OR -
          </p>
          <Button
            style={{color: 'white'}}
            block
            className="ButtonTest"
            onClick={() => handleClick()}
          >
            Get started free!
      </Button>
        </form>
      </div>
    );
  }
}