import React from 'react';
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from 'mdbreact';




const Quoteswitcher = () => {
  const rand = Math.floor(Math.random() * 4) + 1;
  if (rand === 1) {
    return <React.Fragment>
      <p> “It is health that is real wealth and not pieces of gold and silver.” </p>
      <p> - Mahatma Gandhi</p>
    </React.Fragment>;
  }
  if (rand === 2) {
    return <React.Fragment>
      <p> “Health is not valued till sickness comes.” </p>
      <p> - Thomas Fuller</p>
    </React.Fragment>;
  }
  return <React.Fragment>
  <p> “Health is the greatest gift.” </p> 
  <p>- Buddha </p>
  </React.Fragment>
};

const Footer = () => {
  return (
    <MDBFooter color="blue" className="font-small pt-3 mt-1" style={{ alignItems: 'center' }}>
      <MDBContainer fluid className="text-center">
        <MDBRow>
          <MDBCol md="12">
            <h5 className="title">
              <a href="https://prepit.org">
              Prepit
              </a>
            </h5>

              {Quoteswitcher()}
            
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      <div className="footer-copyright text-center py-3">
        <MDBContainer fluid>
          &copy; {new Date().getFullYear()} Copyright: <a href="http://cryptocurrensea.io"> Cryptocurrensea </a>
        </MDBContainer>
      </div>
    </MDBFooter>
  );
}

export default Footer;