const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={(props) => (
  auth.isAuthenticated === true
    ? <Component {...props}/>
    : <Redirect to='/Home' />
  )}/>
)

// const AuthButton = () => (
