function Calc(Budget, Height, Weight,  Age, Gender) {
  const heSpend = (Budget * .15);
  const leSpend = (Budget * .12);
  const mfBudget = ((leSpend + heSpend) / 2).toFixed(2);
  const wBudget = (mfBudget / 4).toFixed(2);
  const dBudget = (wBudget / 7).toFixed(2);
  // lbs to kg
  const metricWeight = Weight * 0.45359237;
  // inches to m
  const metricHeight = Height * 0.0254;
  // Women: BMR = 655 + (4.35 x weight in pounds) + (4.7 x height in inches) - (4.7 x age in years)
  // Men: BMR = 66 + (6.23 x weight in pounds) + (12.7 x height in inches) - (6.8 x age in years)

  function BMR(Gender, Weight, Height){
    if(Gender === 'Male'){
      let BMRmale = (66 + (6.23 * Weight) + (12.7 * Height) - (6.8 * Age)).toFixed(2);
      return <p> {BMRmale} Calories </p>;
    } 
    if (Gender === 'Female') 
    {
      let BMRfemale = (655 + (4.35 * Weight) + (4.7 * Height) - (4.7 * Age)).toFixed(2);
      return <p> {BMRfemale} Calories </p>;
    } 
  }

  function BMI(Weight, Height){
    let Heightsquared = (Height * Height);
    let BMIvalue = ((Weight / Heightsquared) * 703).toFixed(2);
    return BMIvalue
  }

  // The body fat percentage formula, using your BMI, age, and gender:

  // Child Body Fat % = (1.51 x BMI) – (0.70 x Age) – (3.6 x gender) + 1.4

  // Adult Body Fat % = (1.20 x BMI) + (0.23 x Age) – (10.8 x gender) – 5.4

  // using gender male = 1, female = 0.
  function BFpercentage(Gender, Age, BMIvalue) {
    if(Gender === 'Male' && Age >= 18 ){
      let AdultMaleBodyFat = ((1.20 * BMIvalue) + (0.23 * Age) - (10.8 * 1) - 5.4).toFixed(2);
      return <p>{AdultMaleBodyFat} %</p>;
    } 
    if (Gender === 'Female' && Age >= 18 ){
      let AdultFemaleBodyFat = ((1.20 * BMIvalue) + (0.23 * Age) - (10.8 * 0) - 5.4).toFixed(2);
      return <p>{AdultFemaleBodyFat} %</p>;
      
    }
    if (Gender === 'Male' && Age < 18) {
      let ChildMaleBodyFat = ((1.51 * BMIvalue) - (0.70 * Age) - (3.6 * 1) + 1.4).toFixed(2);
      return <p>{ChildMaleBodyFat} %</p>;
    }
    if (Gender === 'Female' && Age < 18) {
      let ChildFemaleBodyFat = ((1.51 * BMIvalue) - (0.70 * Age) - (3.6 * 0) + 1.4).toFixed(2);
      return <p>{ChildFemaleBodyFat} %</p>;
    }
}

  function HealthScore(Gender, Age, BMR, BMIvalue, BFpercentage) {
    if (Gender === 'Male' && Age >= 18) {

      if(BMIvalue < 18){
        return <p className="Ok"> You are under weight </p>
      }
      if( 18 <= BMIvalue <= 24 ){
        return <p className="Good"> You have healthy weight </p>
      }
      if(25 <= BMIvalue <= 29 ){
        return <p className="Poor"> You are overweight </p>
      }
      if(30 <= BMIvalue <= 39) {
        return <p className="Bad"> You are obese </p>
      }
      if (40 <= BMIvalue ) {
        return <p className="Terrible"> You are extremely obese </p>
      }
    }
    if (Gender === 'Female' && Age >= 18) {
      if (BMIvalue < 18) {
        return <p className="Ok"> You are under weight </p>
      }
      if (18 <= BMIvalue <= 24) {
        return <p className="Good"> You have healthy weight </p>
      }
      if (25 <= BMIvalue <= 29) {
        return <p className="Poor"> You are overweight </p>
      }
      if (30 <= BMIvalue <= 39) {
        return <p className="Bad"> You are obese </p>
      }
      if (40 <= BMIvalue) {
        return <p className="Terrible"> You are extremely obese </p>
      }
    }
    if (Gender === 'Male' && Age < 18) {
      if (BMIvalue < 18) {
        return <p className="Ok"> You are under weight </p>
      }
      if (18 <= BMIvalue <= 24) {
        return <p className="Good"> You have healthy weight </p>
      }
      if (25 <= BMIvalue <= 29) {
        return <p className="Poor"> You are overweight </p>
      }
      if (30 <= BMIvalue <= 39) {
        return <p className="Bad"> You are obese </p>
      }
      if (40 <= BMIvalue) {
        return <p className="Terrible"> You are extremely obese </p>
      }
    }
    if (Gender === 'Female' && Age < 18) {
      if (BMIvalue < 18) {
        return <p className="Ok"> You are under weight </p>
      }
      if (18 <= BMIvalue <= 24) {
        return <p className="Good"> You have healthy weight </p>
      }
      if (25 <= BMIvalue <= 29) {
        return <p className="Poor"> You are overweight </p>
      }
      if (30 <= BMIvalue <= 39) {
        return <p className="Bad"> You are obese </p>
      }
      if (40 <= BMIvalue) {
        return <p className="Terrible"> You are extremely obese </p>
      }
    }
  }
    return (
      <React.Fragment>
      
        <h1> About You:</h1>
        <p className="Text"> Your overall Health Status: </p>
        {HealthScore(Gender, Age, BMR(Gender, Weight, Height),BMI(Weight, Height),BFpercentage(Gender, Age, BMI(Weight, Height)) )}
        <p className="Text"> Weight in kilogram (kg): {metricWeight}</p>
        <p className="Text"> Height in meters (m): {metricHeight}</p>

        <h1 style={{ color: 'white', marginTop: '40px'  }}> BMR </h1>
        <p className="Text">{BMR(Gender, Weight, Height)}</p>

        <h1 style={{ color: 'white', marginTop: '40px'  }}> BMI </h1>
        <p className="Text">{BMI(Weight, Height)}</p>

        <h1 style={{ color: 'white', marginTop: '40px' }}> Body Fat Percentage </h1>
        <p className="Text">{BFpercentage(Gender, Age, BMI(Weight, Height))}</p>

        <h1 style={{ color: 'white', marginTop: '40px'  }}> Recomeneded Budget </h1>
        <p className="Text">
          Monthly Food Expense: ${mfBudget}
        </p>
        <p className="Text">
          Weekly Food Expense: ${wBudget}
        </p>
        <p className="Text">
          Daily Food Expense: ${dBudget}
        </p>  
      </React.Fragment>
  );
}


export { Calc };